.container {
  margin-bottom: -1px;
  transition-duration: 0.3s;
  border-top: 1px solid #e6ecf0;
  border-bottom: 1px solid #e6ecf0;
}

.container:hover,
.container:focus {
  background-color: #f5f8fa;
}

.container:focus {
  box-shadow: 0 0 0 2px #68ccfa inset;
}

.wrap {
  position: relative;

  padding: 10px;
  padding-left: 70px;
}

.avatar {
  position: absolute;
  left: 0;

  width: 50px;
  height: 50px;
  margin-left: 10px;
}

.header {
  display: flex;
  align-items: top;
}

.name,
.username,
.time {
  margin-right: 5px;
}

.more {
  margin-left: auto;
  width: 26px;
  height: 26px;

  border-radius: 50%;

  fill: #657758;
  cursor: pointer;

  transition-duration: 0.3s;
}

.more:hover,
.more:focus {
  background-color: #d0f0fe;
  fill: #03a9f4;
}

.more:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.more_icon {
  display: inline-block;
  width: 15px;
  height: 15px;

  transform: translateY(2px);
}

.text {
  margin-bottom: 10px;

  font-size: 15px;
}

.image {
  width: 100%;
  height: 290px;

  border: 1px solid #e6ecf0;
  border-radius: 15px;

  overflow: hidden;
}

.list {
  display: flex;
  padding-top: 5px;
  list-style: none;
}

.item {
  width: 25%;
}

.btn {
  display: inline-flex;
  align-items: center;

  cursor: pointer;
}

.btn_icon {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 36px;
  height: 36px;
  margin-right: 3px;

  border-radius: 50%;
  transition-duration: 0.3s;
}

.count {
  color: #657786;
  font-size: 13px;
  transition-duration: 0.3s;
}

.btn_comment:hover .btn_icon,
.btn_comment:focus .btn_icon,
.btn_share:hover .btn_icon,
.btn_share:focus .btn_icon {
  background-color: #d0f0fe;
  fill: #03a9f4;
}

.btn_comment:hover .count,
.btn_comment:focus .count,
.btn_share:hover .count,
.btn_share:focus .count {
  color: #03a9f4;
}

.btn_comment:focus .btn_icon,
.btn_share:focus .btn_icon {
  box-shadow: 0 0 0 2px #68ccfa;
}

.btn_repost:hover .btn_icon,
.btn_repost:focus .btn_icon {
  background-color: #effcef;
  fill: #2a9d8f;
}

.btn_repost:hover .count,
.btn_repost:focus .count {
  color: #2a9d8f;
}

.btn_repost:focus .btn_icon {
  box-shadow: 0 0 0 2px #3fd1c0;
}

.btn_like:hover .btn_icon,
.btn_like:focus .btn_icon {
  background-color: #ffe3e0;
  fill: #ff6f59;
}

.btn_like:hover .count,
.btn_like:focus .count {
  color: #ff6f59;
}

.btn_like:focus .btn_icon {
  box-shadow: 0 0 0 2px #f18c7d;
}
