.link {
  position: relative;
  display: flex;
  padding: 15px;
  margin-top: -1px;

  border-top: 1px solid #e6ecf0;
  border-bottom: 1px solid #e6ecf0;

  transition-duration: 0.3s;
}

.link:hover,
.link:focus {
  background-color: #f5f8fa;
}

.link:focus {
  box-shadow: 0 0 0 2px #68ccfa inset;
}

.link_active::after {
  content: "";

  position: absolute;
  top: 0;
  right: -1px;

  width: 3px;
  height: 100%;
  background-color: #03abf4;
}

.avatar {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.wrap {
  flex-grow: 2;
  width: 100px;
}

.name,
.username {
  display: inline-block;
  margin-right: 5px;
}

.message {
  color: #657786;
  font-size: 15px;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.date {
  width: 70px;
  margin-left: auto;

  text-align: right;
}
