.container {
  min-height: 53px;
  max-height: 106px;
  padding: 6px 5px;
  border-top: 1px solid #e6ecf0;
}

.wrap {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 38px;
  height: 38px;
  margin-right: 5px;

  font-size: 0;

  border-radius: 50%;
  fill: #03a9f4;
  cursor: pointer;
  transition-duration: 0.3s;

  transform: translateY(4px);
}

.btn:hover {
  background-color: #d0f0fe;
}

.btn:focus {
  box-shadow: 0 0 0 1.5px #68ccfa;
}

.input {
  display: none;
}

.icon {
  width: 22.5px;
  height: 22.5px;
}

.message {
  flex-grow: 2;
  padding-left: 10px;
  margin-right: 5px;
}

.message_text {
  position: relative;
  bottom: 0;
  left: 0;

  height: 30px;
  max-height: 90px;
  width: 100%;
  padding: 4px 10px 5px;

  font-size: 15px;

  background-color: #e6ecf0;
  border: 1px solid transparent;
  border-radius: 25px;

  overflow: hidden;
  resize: none;
}

.message_text:focus {
  background-color: #ffffff;
  border-color: #03a9f4;
}
