.container {
  position: relative;
  width: 100%;
}

.input {
  width: 100%;
  padding: 9px;
  padding-left: 50px;

  font-size: 15px;

  background-color: #e6ecf0;
  border-radius: 20px;
  border: 1px solid transparent;
  outline: none;
}

.icon {
  position: absolute;
  left: 10px;
  top: 50%;

  margin-top: -9px;

  fill: #657786;
}

.input:focus {
  background-color: #ffffff;
  border-color: #03a9f4;
}

.input:focus + .icon {
  fill: #03a9f4;
}
