.container {
  height: 100%;
}

.content {
  height: calc(100% - 103px);

  margin-top: -1px;
  border-top: 1px solid #ffffff;
}

.wrap {
  width: 100%;
  max-width: 600px;
  height: 100%;
  margin: 0 auto;

  border-left: 1px solid #e6ecf0;
  border-right: 1px solid #e6ecf0;

  overflow: scroll;
}

.tab {
  border-bottom: 1px solid #e6ecf0;
}

.tab_wrap {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.tabBtn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50px;

  color: #657786;
  font-size: 15px;
  font-weight: 700;
  text-align: center;

  border-bottom: 2px solid transparent;

  transition-duration: 0.3s;
  cursor: pointer;
  outline: none;
}

.tabBtn_active {
  color: #03a9f4;
  border-color: #03a9f4;
}

.tabBtn:hover {
  color: #03a9f4;
  background-color: #d0f0fe;
}

.tabBtn:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}
