.container {
  display: flex;
}

.wrap {
  width: 600px;

  border-left: 1px solid #e6ecf0;
  border-right: 1px solid #e6ecf0;
}

.content {
  position: relative;

  height: calc(100vh - 55px);
  overflow: hidden;
}

.bg_image {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.content_wrap {
  padding: 10px 15px;
}

.avatar {
  position: absolute;
  top: 120px;
  left: 15px;

  width: 142px;
  height: 142px;

  border: 4px solid #ffffff;
  border-radius: 50%;

  cursor: pointer;
  transition-duration: 0.3s;
}

.avatar img {
  transition-duration: 0.3s;
}

.avatar:hover img {
  filter: brightness(80%);
}

.avatar:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.setup_wrap {
  text-align: right;
  margin-bottom: 15px;
}

.setup {
  display: inline-block;
  padding: 10px 15px;

  color: #03abf4;
  font-size: 15px;
  font-weight: 700;
  text-align: center;

  border: 1px solid #03abf4;
  border-radius: 25px;

  transition-duration: 0.3s;
}

.setup:hover {
  background-color: #d0f0fe;
}

.setup:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.name {
  font-size: 19px;
  font-weight: 800;
}

.status {
  padding: 5px 0;
}

.joined {
  display: flex;
  align-items: center;

  padding: 10px 0;
  color: #657786;
  font-size: 15px;
}

.joined_icon {
  display: inline-block;
  width: 18.75px;
  height: 18.75px;
  margin-right: 5px;

  fill: #657786;
}

.joined_wrap {
  display: inline-block;
  margin-left: 5px;
}

.following {
  float: left;
  margin-right: 15px;
}

.wrap {
  margin-bottom: 15px;
}

.footer {
  padding-left: 10px;
}
