.container {
  display: flex;
  padding: 10px 15px;

  border-bottom: 9px solid #e6ecf0;
}

.avatar {
  width: 50px;
  height: 50px;

  margin-right: 15px;
}

.wrap {
  flex-grow: 2;
}

.message_content {
  width: 100%;
  padding-top: 17px;

  font-size: 19px;
  overflow: hidden;
  resize: none;
}

.message_content::placeholder {
  font-weight: 400;
  opacity: 0.8;
}

.inner_wrap {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
}

.col {
  display: flex;
  align-items: center;
}

.col1 {
  justify-content: flex-start;
}

.col2 {
  justify-content: flex-end;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 38px;
  height: 38px;
  margin-right: 5px;

  font-size: 0;

  border-radius: 50%;
  fill: #03a9f4;
  cursor: pointer;
  transition-duration: 0.3s;
}

.btn:hover {
  background-color: #d0f0fe;
}

.btn:focus {
  box-shadow: 0 0 0 1.5px #68ccfa;
}

.input {
  display: none;
}

.icon {
  width: 22.5px;
  height: 22.5px;
}

.progress {
  width: 20px;
  height: 20px;
  margin-right: 15px;
}

.submit {
  width: 74px;
  height: 39px;

  color: #ffffff;
  font-size: 15px;
  font-weight: 700;

  border-radius: 25px;
  background-color: #03a9f4;
  cursor: pointer;
  transition-duration: 0.3s;
}

.submit:hover,
.submit:focus {
  background-color: #039ce1;
}
