.wrap {
  width: 38px;
  height: 38px;

  border-radius: 50%;

  transition-duration: 0.3s;
}

.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  fill: #03abf4;
}

.wrap:hover {
  background-color: #d0f0fe;
}

.wrap:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}
