.container {
  display: flex;
  align-items: center;
  height: 55px;
  padding: 0 15px;

  border-bottom: 1px solid #e6ecf0;
}

.button_back {
  float: left;

  display: inline-block;
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.wrap {
  padding-left: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.title {
  font-size: 19px;
  font-weight: 800;
}

.intro {
  color: #657786;
  font-size: 13px;
}
