.container {
  display: flex;
  align-items: center;

  width: 218px;
  height: 60px;
  margin-top: auto;
  margin-bottom: 10px;
  padding: 5px 10px;

  border-radius: 30px;
  transition-duration: 0.3s;
  cursor: pointer;
}

.container:hover {
  background-color: #d0f0fe;
}

.container:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.avatar {
  width: 40px;
  height: 40px;

  margin-right: 10px;
}

.wrap {
  width: 120px;
}

.btn {
  display: inline-block;
  width: 9px;
  height: 9px;
  margin-left: auto;

  border-bottom: 1.5px solid #000000;
  border-right: 1.5px solid #000000;

  transform: rotate(45deg);
}
