.container {
  display: flex;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: 10px 15px;
  margin-bottom: -1px;

  border-top: 1px solid #e6ecf0;
  border-bottom: 1px solid #e6ecf0;
  background-color: #f5f8fa;
  transition-duration: 0.3s;
}

.container:hover {
  background-color: #e6ecf0;
}

.container:focus {
  box-shadow: 0 0 0 2px #68ccfa inset;
}

.avatar {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.wrap {
  flex-grow: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.col1 {
  flex-grow: 2;
  width: 100px;
}

.btn {
  padding: 4px 14px;

  color: #03abf4;
  font-size: 15px;
  font-weight: 700;

  border: 1px solid #03abf4;
  border-radius: 25px;

  cursor: pointer;
  transition-duration: 0.3s;
}

.btn:hover {
  background-color: #d0f0fe;
}

.btn:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}
