.container {
  background-color: #ffffff;
  border-bottom: 1px solid #e6ecf0;
}

.wrap {
  display: flex;
  align-items: center;

  width: 100%;
  height: 52px;
  max-width: 600px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  padding-left: 30px;
  font-size: 19px;
  font-weight: 800;
}

.header_inner {
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding-left: 30px;
}
