.container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 52px;
  max-width: 600px;
  margin: 0 auto;
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;

  border-radius: 50%;

  transition-duration: 0.3s;
}

.link:hover {
  background-color: #d0f0fe;
  fill: #03abf4;
}

.link:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.link:hover .icon_base,
.link:focus .icon_base {
  fill: #03abf4;
}

.icon {
  width: 26.25px;
  height: 26.25px;
}

.icon_base {
  fill: #657786;
}

.icon_active {
  display: none;
  fill: #03abf4;
}

.link_active .icon_base {
  display: none;
}

.link_active .icon_active {
  display: inline-block;
}
